import React, {useEffect, useState} from "react";
import ArrowUp from '../../media/images/arrow_up.png';
import "./footerUI.scss";

function FooterUI() {
    const [showGoTop, setShowGoTop] = useState<boolean>(false);

    const listenToScroll = () => {
        const winScroll =
            document.body.scrollTop || document.documentElement.scrollTop;

        if (winScroll > 80) {
            setShowGoTop(true);
        }

        if (winScroll <= 80) {
            setShowGoTop(false);
        }
    };

    useEffect(() => {
        window.addEventListener("scroll", listenToScroll);
    }, []);

    return (
        <>
            {showGoTop && (
                <div className="back-button-wrapper">
                    <div
                        className="back-button"
                        onClick={() => {
                            window.scrollTo(0, 0);
                        }}
                    >
                        <img alt="back to top" src={ArrowUp}/>
                    </div>
                </div>
            )}
            <footer>
                <div className="footer-helper">
                    <div className="headline"><span>PORTFOLIO WEBSITE</span></div>
                    <div className="links">
                        <ul>
                            <li>
                                <a href="#personal">Главная</a>
                            </li>
                            <li>
                                <a href="#projects">Проекты</a>
                            </li>
                            <li>
                                <a href="#contact">Контакты</a>
                            </li>
                        </ul>
                    </div>
                </div>
            </footer>
        </>
    );
}

export default FooterUI;
