import React from "react";
import Form from "../form";
import PersonalImage from "../../media/images/personal2.jpg";
import "./mainUI.scss";
import nodeJSIcon from "../../media/images/node-js-icon.png";
import reactIcon from "../../media/images/react-icon.png";
import scssIcon from "../../media/images/sass-icon.png";
import expressIcon from "../../media/images/express-icon.png";
import musicIcon from "../../media/images/musicology.png";
import fmIcon from "../../media/images/frontend-mentor.png";
import fwIcon from "../../media/images/fun_weather.png";

import Button from './../button';

function MainUI(props: any) {
    return (
        <div className="content">
            <div className="content-helper">
                {/* WEB */}
                <section id="personal">
                    <div className="headline">
                        WE<span>B</span>
                    </div>
                    <div className="about-block">
                        <div className="description">
                            <p>
                                <h3>
                                    Тарасенко Илья, {" "}
                                    {props.getAge()} лет
                                </h3>
                                <p>
                                    Разрабатываю веб-приложения, сервисы и
                                    утилиты. При создании сайтов использую
                                    последний стек технологий и популярные
                                    библиотеки при построении интерфейса.
                                    Стремлюсь получить хороший опыт, и дальше
                                    продолжать развиваться и усовершенствовать
                                    навыки в данной сфере.
                                </p>
                            </p>
                        </div>
                        <div className="image">
                            <img alt="" src={PersonalImage}/>
                        </div>
                    </div>
                </section>
                {/* EMPTY */}
                <section id="projects">
                    <div className="headline">Проекты</div>
                    <div className="project-block">
                        <div className="project-group">
                            <div className="project-item">
                                <div className="title">Musicology</div>
                                <div className="image">
                                    <a href="https://violenceeke.github.io/musicology/" target="_blank">
                                        <img src={musicIcon}/>
                                    </a>
                                </div>
                                <div className="description">
                                    Лендинг "Музыковедение".
                                </div>
                            </div>
                            <div className="project-item">
                                <div className="title">Диаграмма-компоненты</div>
                                <div className="image">
                                    <a href="https://violenceeke.github.io/expenses-chart-components/"
                                       target="_blank">
                                        <img src={fmIcon}/>
                                    </a>
                                </div>
                                <div className="description">
                                    Челендж с Frontend Mentor.
                                </div>
                            </div>
                            <div className="project-item">
                                <div className="title">Fun Weather</div>
                                <div className="image">
                                    <a href="https://violenceeke.github.io/fun-weather/"
                                       target="_blank">
                                        <img src={fwIcon}/>
                                    </a>
                                </div>
                                <div className="description">
                                    Лендинг мобильного приложения о погоде.
                                </div>
                            </div>
                        </div>
                        <div className="button-wrapper">
                            <a href="https://github.com/violenceeke" className="custom-button">Ещё</a>
                        </div>
                    </div>
                </section>
                {/* TECHNOLOGY */}
                <section id="technologies">
                    <div className="headline">Технологии</div>
                    <div className="technology-block">
                        <div className="tech-item">
                            <div className="title">React</div>
                            <div className="image">
                                <img src={reactIcon}/>
                            </div>
                            <div className="description">JavaScript библиотека для создания пользовательских
                                интерфейсов
                            </div>
                        </div>
                        <div className="tech-item">
                            <div className="title">NodeJS</div>
                            <div className="image">
                                <img src={nodeJSIcon}/>
                            </div>
                            <div className="description">Cистема для выполнения JavaScript отдельно от вашего
                                браузера.
                            </div>
                        </div>
                        <div className="tech-item">
                            <div className="title">SASS</div>
                            <div className="image">
                                <img src={scssIcon}/>
                            </div>
                            <div className="description">Метаязык на основе CSS, предназначенный для увеличения уровня
                                абстракции CSS-кода
                            </div>
                        </div>
                        <div className="tech-item">
                            <div className="title">Express</div>
                            <div className="image">
                                <img src={expressIcon}/>
                            </div>
                            <div className="description">Минималистичный и гибкий веб-фреймворк для приложений Node.js
                            </div>
                        </div>
                    </div>
                </section>
                {/* CONTACT ME */}
                <section id="contact">
                    <div className="headline">Свяжись со мной</div>
                    <div className="contact-block">
                        <Form onSubmit={(e: any) => e.preventDefault()}>
                            <div className="form-group">
                                <input type="text" placeholder="Имя"/>
                                <input type="text" placeholder="Фамилия"/>
                            </div>
                            <div className="form-group">
                                <input type="text" placeholder="E-mail"/>
                            </div>
                            <div className="form-group">
                                <textarea rows={10} placeholder="Сообщение..."/>
                            </div>
                            <div className="form-group">
                                <Button fluid={true}>Отправить</Button>
                            </div>
                        </Form>

                    </div>
                </section>
            </div>
        </div>
    );
}

export default MainUI;
