import React from "react";
import MainUI from "../../components/mainUI";
import {Helmet} from "react-helmet";

function Main() {
    const getAge = () => {
        return Math.floor(
            (new Date(2022, 10, 7).getTime() -
                new Date(2001, 10, 7).getTime()) /
            (1000 * 60 * 60 * 24) /
            31 /
            12
        );
    };

    return (
        <>
            <Helmet>
                <meta charSet="utf-8"/>
                <title>Cайт портфолио | Тарасенко Илья</title>
            </Helmet>
            <MainUI getAge={getAge}/>
        </>
    );
}

export default Main;
